.App {
  text-align: center;
}

.title {
  font-size: 2rem;
  color: rgb(226, 55, 55);
  font-weight: 700;
}

.text {
  display: flex;
  justify-content: center;
  max-width: 800px;
  margin: 0 auto;
  padding-top: 1rem;
  color: #121212;
}

.banner {
  max-height: 250px;
  overflow-y: hidden;
  padding-top: 2rem;
}

.btn-link {
  margin-top: 1rem;
}

.btn-link button {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 900;
  padding: 1.25rem 2rem;
  font-size: 1rem;
  border-radius: 4px;
  position: relative;
  min-width: 15rem;
  max-width: 90vw;
  overflow: hidden;
  border: 0;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background: #b721ff;
  color: #fff;
}

.btn-link button:hover {
  background: #5c1080;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
